/*
Josh's Custom CSS Reset
https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

small {
  font-size: var(--wp--preset--font-size--small);
}

ul,
ol {
  padding: 0 0 0 var(--wp--preset--spacing--50);
}

.visually-hidden:not(:focus):not(:active):not(:focus):not(:active) {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.always-visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

input[type=submit] {
  appearance: none;
  background-color: var(--wp--preset--color--primary);
  border-radius: 0px;
  border-color: var(--wp--preset--color--background);
  border-style: solid;
  border-width: 2px;
  color: var(--wp--preset--color--background);
  padding-top: var(--wp--preset--spacing--20);
  padding-right: var(--wp--preset--spacing--50);
  padding-bottom: var(--wp--preset--spacing--20);
  padding-left: var(--wp--preset--spacing--50);
  text-decoration: none;
}

.wp-site-blocks > header {
  z-index: 999;
  margin: 0 auto;
  border-bottom: 1px solid rgba(159, 123, 78, 0.55);
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.35);
}

body.admin-bar header {
  top: 32px;
}

main,
.woocommerce div.product {
  margin-top: 9em;
}

.wp-block-categories-list {
  list-style-type: none;
  padding-left: 0;
  width: 100%;
}
.wp-block-categories-list .cat-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--wp--preset--color--zeever-bodytext);
  padding: 13px 0 13px 0;
}
.wp-block-categories-list .cat-item a {
  flex-grow: 1;
  text-decoration: none;
  font-weight: 600;
  color: #D0D0D0;
}

.wp-block-search__input {
  background: transparent;
  padding: 0 20px;
}
.wp-block-search__inside-wrapper {
  margin-bottom: var(--wp--preset--spacing--50);
}

.wp-block-tag-cloud .tag-cloud-link {
  position: relative;
  padding: 12px 20px 14px 20px;
  transition: color 0.3s, border 0.3s;
  margin: 10px 10px 0 0;
  text-transform: none;
  border-style: solid;
  border-width: 1px;
  border-radius: 0;
  font-size: 14px !important;
  line-height: 1.2 !important;
  color: var(--wp--preset--color--zeever-bodytext);
  text-decoration: none;
  font-weight: 600;
  border-color: var(--wp--preset--color--zeever-bodytext);
  transition: background-color 0.3s, color 0.3s, border 0.3s;
}
.wp-block-tag-cloud .tag-cloud-link:hover {
  background: rgba(230, 230, 230, 0.062745098);
}

.wp-block-cloudcatch-splide-carousel .splide__pagination {
  bottom: 6em;
}
.wp-block-cloudcatch-splide-carousel .splide__arrow--prev {
  left: calc(2em + 30px) !important;
}
.wp-block-cloudcatch-splide-carousel .splide__arrow--next {
  right: calc(2em + 30px) !important;
}

:root {
  --zeever-transition: cubic-bezier(0.165, 0.84, 0.44, 1) ;
}

body .zeever-animate-init {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) translateX(0) scale(1) rotate(0);
}

.zeever-animate {
  opacity: 0;
  visibility: hidden;
  transition-property: opacity, transform;
}

.zeever-move-up {
  transform: translateY(30px);
}

.zeever-move-down {
  transform: translateY(-30px);
}

.zeever-move-right {
  transform: translateX(-40px);
}

.zeever-move-left {
  transform: translateX(40px);
}

.zeever-flip {
  transform: perspective(500px) translateY(60px) scaleY(0.75) rotateX(-40deg);
}

.zeever-scale {
  transform: scale(0.5);
}

.zeever-flip,
.zeever-move-down,
.zeever-move-up,
.zeever-move-right,
.zeever-move-left,
.zeever-scale {
  transition-duration: 0.7s;
  transition-timing-function: var(--zeever-transition);
}

.zeever-delay-1 {
  transition-delay: 0.1s;
}

.zeever-delay-3 {
  transition-delay: 0.3s;
}

.zeever-delay-5 {
  transition-delay: 0.5s;
}

.zeever-delay-7 {
  transition-delay: 0.7s;
}

.zeever-delay-10 {
  transition-delay: 1s;
}

.zeever-delay-20 {
  transition-delay: 2s;
}